import React, { useState, useEffect } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Card from "./card";
import Register from "./register";
import { customApiClient } from "../../shared/apiClient";

import ic_pay_cardtab from "../../assets/ic_pay_cardtab.svg";
import ic_pay_cardtab_g from "../../assets/ic_pay_cardtab_g.svg";

const Slide = ({ setCardIdx }) => {
  //state
  const [cardData, setCardData] = useState([]);
  const [currentSlide, setCurrentSlide] = useState(0);

  //life cycle
  useEffect(async () => {
    const data = await customApiClient("get", "/party/user/card");

    //
    if (!data) return;

    //
    if (data.statusCode !== 200) {
      return;
    }

    setCardData(data.result);

    //  
    if (data.result.length === 0) {
      setCardIdx(-1);
    }
    //  1     
    else {
      setCardIdx(data.result[0].idx);
    }

  }, []);

  //    cardIdx 
  useEffect(() => {
    if (cardData.length === 0) return

    if (cardData.length === currentSlide) {
      setCardIdx(-1);
    }
    else {
      setCardIdx(cardData[currentSlide].idx);
    }
  }, [currentSlide]);

  return (
    <div>
      {cardData.length === 0 ? (
        <Register />
      ) : (
          <Slider
            {...settings}
            afterChange={(current) => {
              setCurrentSlide(current);
            }}
          >
            {cardData.map((cardData, index) => {
              return (
                <div key={cardData.idx}>
                  <Card
                    cardImg={currentSlide === index ? ic_pay_cardtab : ic_pay_cardtab_g}
                    cardName={cardData.cardName}
                    cardNo={cardData.cardNo}
                  />
                </div>
              );
            })}
            <Register card={true}/>
          </Slider>
        )}
    </div>
  );
};

export default Slide;

export const settings = {
  className: "center",
  dots: false,
  centerMode: true,
  infinite: false,
  centerPadding: "2%", //px slick-slider  ..
  slidesToshow: 2,
  speed: 400,
  focusOnSelect: true,
  // beforeChange: (crruent, next) => setImageIndex(next)
};


