import React, { useContext, useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useDispatch } from "react-redux";
import styled from "styled-components";

import { PageTransContext } from "../../containers/pageTransContext";
import { ContentWrap, HeaderWrap, PageWrap } from "../../styled/shared/wrap";
import { TextMiddle } from "../../styled/shared";
import { customApiClient } from "../../shared/apiClient";
import { BottomNavCloseAction } from "../../reducers/container/bottomNav";
import { AddButton, CardWrap } from "./cardManagement";

import icon_back from "../../assets/icon-back-arrow.svg";
import blank_duck from "../../assets/ic_cardadmin_duck@2x.png"
import BankComponent from "./bankComponent";

const BankAccountManagement = () => {
  const dispatch = useDispatch();
  const history = useHistory();

  //context
  const { setPageTrans } = useContext(PageTransContext);

  //state
  const [cardData, setCardData] = useState([]);

  const closePage = () => {
    setPageTrans("trans toLeft");
    history.goBack();
  };

  const gotoRegister = () => {
    setPageTrans("trans toRight");
    history.push("/card");
  };

  useEffect(async () => {
    dispatch(BottomNavCloseAction);
  }, []);

  //life cycle
  useEffect(async () => {
    const data = await customApiClient("get", "/party/user/bankAccount");
    //
    if (!data) return;

    if (data.statusCode == 200) {
      setCardData(data.result);
      console.log(data);
    }

    //
    if (data.statusCode != 200) {
      return;
    }
  }, []);

  return (
    <div className="page">
      <PageWrap>
        <HeaderWrap id="back_link" className="spoqaBold" onClick={closePage}>
          <div
            style={{
              position: "absolute",
              top: "55%",
              left: "1.25rem",
              transform: "translate(0,-55%)",
            }}
          >
            <img src={icon_back}></img>
          </div>
          <TextMiddle> </TextMiddle>
        </HeaderWrap>
        <ContentWrap>
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "space-between",
              margin: "1rem 0 1.125rem 0",
            }}
          >
            <div className="spoqaBold" style={{ fontSize: "0.875rem" }}>
               
            </div>
            <AddButton className="notoMedium" onClick={gotoRegister}><span>+  </span></AddButton>
          </div>
          {cardData.length === 0 ? (
            <CardWrap>
              <img src={blank_duck} />
              <div className="notoBold title-text">  .</div>
              <div className="notoMedium sub-text"> "" <br />  .</div>
            </CardWrap>
          ) : (
              <div>
                {cardData.map((card) => {
                  return (
                    <div key={card.idx}>
                      <BankComponent
                        cardName={card.bankName}
                        cardNo={card.bankAccountNum}
                        id={card.idx}
                        cardData={cardData}
                        setCardData={setCardData}
                      ></BankComponent>
                    </div>
                  );
                })}
              </div>
            )}
        </ContentWrap>
      </PageWrap>
    </div>
  );
};


export default BankAccountManagement;
